.App {
  text-align: center;
}
.main_block {
  background-color: #1D8B77;
  height: 1000px;
  display: flex;
  padding-top: 20px;
  flex-direction: row;
  justify-content: space-around;
}
.root {    
  display: inline-block;    
  vertical-align: top;
  background-color: #D4D4D4;
  width: 28vw;
  height: fit-content;  
  padding: 20px
}
.plus {
  font-size: 30px;
  display: inline-block;
  padding: 0 10px;
}
.input_text {
  padding: 10px; 
  font-size: medium;
  background-color: #fff;
  width: 100%;
  margin: 10px 0 ;  
}

.backet {
  display: block;
  text-align: center;
  cursor: pointer;
}
@media (max-width: 1000px) {
  .main_block {
   
    display: block;
  }
  .root {
    width: 80vw;
    display: block;
    margin: 10px auto;
  }

}      

